import $ from 'jquery';
import { getConfigFile } from '../ablibary';
import "./sideBarLink.css"
export class SideBarLink {
    constructor() {
        this._links = [
            {
                img: 'images/sidebarIcons/gmail.jpg',
                title: 'gmail',
                url: 'https://gmail.com/'
            },
            {
                img: 'images/sidebarIcons/youtube.jpg',
                title: 'youtube',
                url: 'https://www.youtube.com/'
            },
            {
                img: 'images/sidebarIcons/facebook.jpg',
                title: 'facebook',
                url: 'https://www.facebook.com/'
            },
            {
                img: 'images/sidebarIcons/instagram.jpg',
                title: 'instagram',
                url: 'https://www.instagram.com/'
            },
            {
                img: 'images/sidebarIcons/pinterest.jpg',
                title: 'pinterest',
                url: 'https://www.pinterest.com/'
            },
            {
                img: 'images/sidebarIcons/twitter.jpg',
                title: 'twitter',
                url: 'https://twitter.com/'
            },
            {
                img: 'images/sidebarIcons/whatsapp.jpg',
                title: 'whatsapp',
                url: 'https://web.whatsapp.com/'
            },
            {
                img: 'images/sidebarIcons/telegram.jpg',
                title: 'telegram',
                url: 'https://web.telegram.org/'
            },
            {
                img: 'images/sidebarIcons/reddit.jpg',
                title: 'reddit',
                url: 'https://reddit.com/'
            },
            
        ];
    }

    ui() {
        const links = this._links.map((link) => {
            return `
                <li class="sideBarLink__area__item">
                    <a href="${link.url}" target="_blank">
                        <img src="${link.img}" alt="${link.title}" />
                    </a>
                </li>
            `;
        }).join('');
        $('body').append(`
            <ul class="sideBarLink__area">${links}</ul>
        `);
    }

    async init() {
        const configaration = await getConfigFile();
        if(configaration.sideBarLink) this.ui();
    }
}