import 'material-icons/iconfont/material-icons.css';
import 'jquery-ui/ui/widgets/draggable.js'
import $ from 'jquery';

import './stickyNote.css';
export class StickyNote {
    constructor(){
        this.list = [];
    }

    ui(){

        //isAnyHidden
        if(this.isAnyHidden()){
            $('.sticky-note__show-all').show();
            $('.sticky-note__hide-all').hide();
        }else{
            $('.sticky-note__show-all').hide();
            $('.sticky-note__hide-all').show();
        }

        // clear all sticky notes
        $('.sticky-note').remove();
        for (let i = 0; i < this.list.length; i++) {
            const item = this.list[i];
            $('body').append(`
                <div id="sticky-note-${item.id}" class="sticky-note" style="top: ${item.position.top}px; left: ${item.position.left}px; background-color: ${item.color}; display: ${item.hidden?'none': 'block'}">
                    <div class="sticky-note__header">
                        <span class="material-icons sticky-note__color">more_vert</span>
                        <ul class="sticky-note__color-list">
                            <li class="sticky-note__color-item" data-color="#fff475">Yellow</li>
                            <li class="sticky-note__color-item" data-color="#C7E1F4">Blue</li>
                            <li class="sticky-note__color-item" data-color="#f5f5f5">Gray</li>
                            <li class="sticky-note__color-item" data-color="#f28b82">Red</li>
                        </ul>
                        <span class="material-icons sticky-note__close">delete</span>
                    </div>
                    <div class="sticky-note__content">
                        <textarea class="sticky-note__textarea" style=" width: ${item.size.width}px; height: ${item.size.height}px;">${item.text}</textarea>
                    </div>
                </div>
            `);

            //make the DIV element draggable:
            $(`#sticky-note-${item.id}`).draggable();
        }

    }


    add(position){
        const self = this;
        const item = {
            id: new Date().getTime(), 
            text: '',
            position: {
                top: position?.top+10 || self.middlePosition().top,
                left: position?.left+10  || self.middlePosition().left,
            },
            size: {
                width: 225,
                height: 170,
            },
            color: '#fff475',
            hidden: false,
        };
        this.list.push(item);
        this.ui();
        this.saveData();
    }

    // middle screen position
    // manage the center position
    middlePosition(){
        return {
            top:($(window).height() - 170) / 2,
            left: ($(window).width() - 225) / 2
        }
    }

    // hide all sticky notes
    hideAll(){
        this.list = this.list.map((item)=>{
            return {
                ...item,
                hidden: true,
            };
        });
        this.ui();
        this.saveData();
    }

    // show all sticky notes
    showAll(){
        this.list = this.list.map((item)=>{
            return {
                ...item,
                hidden: false,
            };
        });
        this.ui();
        this.saveData();
    }

    // check if any sticky note is hidden
    isAnyHidden(){
        return this.list.some((item)=>item.hidden);
    }


    remove(id){
        this.list = this.list.filter((item)=>item.id !== parseInt(id));
        this.ui();
        this.saveData();
    }

    removeAll(){
        this.list = [];
        this.ui();
        this.saveData();
    }

    update(id, data, updateUI = false){
        this.list = this.list.map((item)=>{
            if(item.id === id){
                return {
                    ...item,
                    ...data,
                };
            }
            return item;
        });
        if(updateUI){
            this.ui();
        }
        this.saveData();
    }

    saveData(){
        const self = this;
        chrome.storage.sync.set({stickyNote: this.list}, function() {
            console.log('Value is set to ' + self.list);
        });
    }

    async getSavedData(){
        const self = this;
        return new Promise((resolve, reject)=>{
            chrome.storage.sync.get(['stickyNote'], function(result) {
                if(result.stickyNote){
                    console.log('Value currently is ' + result.stickyNote);
                    self.list = result.stickyNote;
                    resolve();
                }else{
                    self.list = [];
                    resolve();
                }
            });
        });
    }

    addEvent(){
        const self = this;

        // on body click hide stickyNoteMenu
        $(document).on('click', 'body', (e)=>{
            // if target is outside of .stickyNoteMenu then hide .stickyNoteMenu
            $('.stickyNoteMenu').slideUp('fast');
            if(!$(e.target).closest('.stickyNoteMenu').length){
                console.log('outside');
            }
        });


        // aplly z-index
        $(document).on('click, mouseover', '.sticky-note', (e)=>{
            $('.sticky-note').css('z-index', 0);
            $(e.currentTarget).css('z-index', 1);
        });

        // add new sticky note
        $(document).on('click', '.sticky-note__add', (e)=>{
            if(self.list.length && $('.stickyNoteMenu').css('display') == 'none'){
                $('.stickyNoteMenu').slideDown('fast');
                return false;
            }else if(!self.list.length){
                self.add();
            }
        });

        // add new from menu 
        $(document).on('click', '.stickyNoteMenu .sticky-note__add', (e)=>{
            self.add();
        });


        // remove all sticky notes
        $(document).on('click', '.sticky-note__remove-all', (e)=>{
            self.removeAll();
        }); 

        // hide all sticky notes
        $(document).on('click', '.sticky-note__hide-all', (e)=>{
            self.hideAll();
            // $('.sticky-note').hide();
        });

        // show all sticky notes
        $(document).on('click', '.sticky-note__show-all', (e)=>{
            self.showAll();
            // $('.sticky-note').show();
        });


        $(document).on('change', '.sticky-note__textarea', (e)=>{
            const id = parseInt($(e.currentTarget).closest('.sticky-note').attr('id').replace('sticky-note-', ''));
            const text = $(e.currentTarget).val();
            self.update(id, {text});
        });

        // resize
        // on mouse up on sticky save the size and position on target is dropped
        $(document).on('mouseup', '.sticky-note', (e)=>{
            const id = parseInt($(e.currentTarget).attr('id').replace('sticky-note-', ''));
            const position = $(e.currentTarget).position();

            // if target is .sticky-note__close then remove sticky note and return
            if($(e.target).hasClass('sticky-note__close')){
                self.remove(id);
                return;
            }

            // if target is .sticky-note__color 
            if($(e.target).hasClass('sticky-note__color')){
                $(e.target).next().toggle();
                return;
            }

            // if target is .sticky-note__color-item
            if($(e.target).hasClass('sticky-note__color-item')){
                const color = $(e.target).attr('data-color');
                //hide color list
                $(e.target).parent().hide();
                self.update(id, {color}, true);
                return;
            }


            const size = {
                width: $(e.currentTarget).width(),
                height: $(e.currentTarget).height() - 30,
            }
            self.update(id, {position, size});
        });


        // on resize the texarea
        // $('textarea').bind('mouseup mousemove',function(e){
        //     if(this.oldwidth  === null){this.oldwidth  = this.style.width;}
        //     if(this.oldheight === null){this.oldheight = this.style.height;}
        //     if(this.style.width != this.oldwidth || this.style.height != this.oldheight){
        //         $(this).resize();
        //         this.oldwidth  = this.style.width;
        //         this.oldheight = this.style.height;

        //         // get id
        //         const id = parseInt($(e.currentTarget).closest('.sticky-note').attr('id').replace('sticky-note-', ''));
        //         const size = {
        //             width: $(e.currentTarget).width(),
        //             height: $(e.currentTarget).height(),
        //         }
        //         console.log(id)
        //         console.log(size)
        //         self.update(id, {size});

                
        //     }
        // });
    
    }

    async init(){
        await this.getSavedData();
        this.ui();
        this.addEvent();
    }

}
