import $ from 'jquery';
import moment from 'moment';


// weather code
// 0	Clear sky
// 1, 2, 3	Mainly clear, partly cloudy, and overcast
// 45, 48	Fog and depositing rime fog
// 51, 53, 55	Drizzle: Light, moderate, and dense intensity
// 56, 57	Freezing Drizzle: Light and dense intensity
// 61, 63, 65	Rain: Slight, moderate and heavy intensity
// 66, 67	Freezing Rain: Light and heavy intensity
// 71, 73, 75	Snow fall: Slight, moderate, and heavy intensity
// 77	Snow grains
// 80, 81, 82	Rain showers: Slight, moderate, and violent
// 85, 86	Snow showers slight and heavy
// 95 *	Thunderstorm: Slight or moderate
// 96, 99 *	Thunderstorm with slight and heavy hail

export class weather {
    constructor(weatherArea) {
        this.weatherArea = $(weatherArea);
        this.latitude = 40.704776;
        this.longitude = -74.1458494;
        this.city = '';
        this.isCelsius = true;
        this.timezone = 'America/New_York';
        this.data = {}
        this.init()
    }
    
    async renderUi (){
        const self = this;
        const location = await this.checkLocation();
        this.weatherArea.html(`
            <div class="currentWeather">
                <div class="icon">
                    <img src="weatherIcons/day/${self.data.current_weather.weathercode}.png" />
                </div>
                <div class="info">
                    <div class="temp">
                        <span class="temp-value">${self.data.current_weather.temperature}</span>
                        <span class="temp-unit">${self.data.hourly_units.temperature_2m}</span>
                    </div>
                    <div class="location">${self.city}</div>
                    <div class="date">${self.genateDate()}</div>
                    <div class="swicher">
                        <span ${self.isCelsius && 'class="active"'}>C</span> /
                        <span ${!self.isCelsius && 'class="active"'}>F</span>
                        <img src="images/loading-gif.gif" width="16px" class="wetherLoading" style="display: none">
                    </div>
                </div>
                <div class="forecastIcon">
                    <img src="images/icons8-chevron-24.png" />
                </div>
            </div>
            <table class="forecastList">
                ${self.data.daily.time.slice(-6).map((time, index) => {
                    return `
                        <tr class="forecastItem">
                            <td class="date">${self.genateDay(time)}</td>
                            <td class="icon">
                                <img src="weatherIcons/day/${self.data.daily.weathercode[index]}.png" />
                            </td>
                            <td class="temp">
                                <span class="temp-value">${self.data.daily.temperature_2m_max[index]}</span>
                                <span class="temp-unit">${self.data.daily_units.temperature_2m_max}</span>
                            </td>
                            <td class="wind">
                                <span class="wind-value">${self.data.daily.temperature_2m_min[index]}</span>
                                <span class="wind-unit">${self.data.daily_units.temperature_2m_min}</span>
                            </td>  
                        </tr>
                    `;
                }).join('')}
            </table>
            
            ${
                !location? `<button> <span class="material-icons">cloudy_snowing</span>  <span>Show my Weather</span> </button>` : ''
            }
            

        `);

        
    }


    genateDate() {
        const dataTime = this.data.current_weather.time;
        return moment(dataTime).format('dddd, DD');
    }

    genateDay(dataTime) {
        return moment(dataTime).format('dddd');
    }

    async getTimezone() {
        const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone;
        return timezone;
    }

    // get city name using geolocation for free
    async getCityName() {
        const request = await  fetch(`https://api.bigdatacloud.net/data/reverse-geocode-client?latitude=${this.latitude}&longitude=${this.longitude}&localityLanguage=en`);
        const data = await request.json();
        this.city = data.city;
    }

    async getData() {
        const request = await fetch(`
            https://api.open-meteo.com/v1/forecast?
            latitude=${this.latitude}
            &longitude=${this.longitude}
            &timezone=${this.timezone}
            &hourly=temperature_2m,relativehumidity_2m,windspeed_10m
            &daily=weathercode,temperature_2m_max,temperature_2m_min&current_weather=true
            &past_days=7
            &temperature_unit=${this.isCelsius?'celsius':'fahrenheit'}
        `.replace(/\s/g, ''));
        const data = await request.json();
        this.data = data;
        console.log(data);
        return data;
    }

    async checkLocation() {
        const permissionStatus = await navigator?.permissions?.query({name: 'geolocation'})
        const status = permissionStatus?.state // Dynamic value
        if (status === 'granted') {
            return true;
        } else if (status === 'prompt') {
            return false;
        } else if (status === 'denied') {
            return false;
        }
    }


    getUserLocation() {
        const self = this;
        return new Promise((resolve, reject) => {
            navigator.geolocation.getCurrentPosition(function (position) {
                self.latitude = position.coords.latitude;
                self.longitude = position.coords.longitude;
                resolve();
            }, function (error) {  
                console.log(error); 
                resolve();
            });
        });
    }

    setEvent(){
        const self = this;
        $(document).on('click', '.forecastIcon', function(){
            $(this).toggleClass('active');
            $('.forecastList').toggle();
        });

        $(document).on('click', '.swicher', async function() {
            $('.wetherLoading').toggle();
            self.isCelsius = !self.isCelsius;
            await self.getData();
            self.renderUi();
            self.saveTemperatureUnit();
        });

        $(document).on('click', '.weatherArea button', async function() {
            if(navigator.geolocation) {
                await self.getUserLocation();
            }
            self.weatherArea.html(`<img src="images/loading-gif.gif" width="20px" style="display:block; margin: auto">`);
            await self.getData();
            await self.getCityName();
            self.renderUi();
        });
    }

    // save temperature unit
    saveTemperatureUnit(){
        const self = this;
        chrome.storage.sync.set({ 
                'temperatureUnit': self.isCelsius, 
            }, () => {
                console.log('Tasks saved');
            }
        );
    }

    // get temperature unit
    getTemperatureUnit(){
        return new Promise((resolve)=>{
            chrome.storage.sync.get(['temperatureUnit'], function(result) {
                if(result)
                    resolve(result.temperatureUnit);
                else
                    resolve(true);
            });
        })
    }

    async init() {
        this.weatherArea.html(`<img src="images/loading-gif.gif" width="20px" style="display:block; margin: auto">`);
        this.isCelsius = await this.getTemperatureUnit();
        if(navigator.geolocation) {
            const isLocation = await this.checkLocation();
            if(isLocation){
                await this.getUserLocation();
                this.timezone = await this.getTimezone();
            }
        }
        await this.getData();
        await this.getCityName();
        this.renderUi();
        this.setEvent();
    }
}