import $ from 'jquery';
import "./recentlyClosed.css"
export class RecentlyClosed {
    constructor() {
        this._recentlyClosed = [];
    }

    ui(recentlyClosed){
        const self = this;
        const list = recentlyClosed.map((value, index)=>{
            return `
                <li class="recentlyClosed__item">
                    <img src="${self.getFaviconUrl(value.url)}" />
                    <a href="${value.url}" target="__blank">${value.title}</a>
                </li>
            `;
        }).join('');
        $('.recentlyClosed__area').append(list);
    }

    getRecentlyClosed() {
        return new Promise(function (resolve, reject) {
            chrome.history.search({
                text: '',
                maxResults: 10,
                startTime: 0,
                endTime: Date.now()
            }, function (data) {
                var recentlyClosed = data.map((value, index)=>{
                    return {
                        title: value.title,
                        url: value.url
                    }
                }); 
                resolve(recentlyClosed);
            });
        });
    }   

    // chrome.permissions.request
    getPermissions() {
        return new Promise(function (resolve, reject) {
            chrome.permissions.request({
                permissions: ['history', 'favicon']
            }, function (granted) {
                if (granted) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            });
        });
    }

    getFaviconUrl(url) {
        return `chrome-extension://${chrome.runtime.id}/_favicon/?pageUrl=${encodeURIComponent(url)}&size=16`;
    }

    addEvents(){
        const self = this;
        $(document).on('click', '.recentlyClosed_item', async (e)=>{
            $('.recentlyClosed__area').toggle();
            $('.recentlyClosed__area').html('');
            const permissions = await self.getPermissions();
            if (permissions) {
                const recentlyClosed = await self.getRecentlyClosed();
                self.ui(recentlyClosed);
            } else {
                console.log('permissions not granted');
            }
        });

        // if click outside of the element, hide it
        $(document).on('click', function (e) {
            if (!$(e.target).closest('.recentlyClosed_item').length) {
                $('.recentlyClosed__area').hide();
            }
        });
    }
           

    async init(){
        this.addEvents();
    }
}