import $ from 'jquery';
import anime from 'animejs';
// import { VersionMenagement } from './versionMenagement';
import { getConfigFile } from './ablibary';
import { FileMenagement } from './fileMenagement';


const fileMenagement = new FileMenagement();
export class Themes {
    constructor(){
        this.quoteAreaBoxBgStatus = false;
        this.quoteAreaBoxBgColor = '#1c1c1cb5';
        this.quoteAreaBoxTextColor = '#fff';
        this.authorTextColor = '#fff';
        this.quoteArea = $('.quote');
        this.authorArea = $('.author');
        this.backgroundArea = $('.bg-section'); 
        // this.quoteList = [];
        // this.backgrounds = [];
        this.quoteFontList = [];
        this.authorFontList = [];
        this.quoteFontSizes  =  [];
        this.authorFontSizes =  [];
        this.theme = {
            quote: '',
            background: '',
            quoteFont: '',
            authorFont: '',
            quoteFontSize: '',
            authorFontSize: '',
        };
    }

    delay(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }
    
    async setTheme(isReaload){

        // 0 0 10px rgb(0 0 0 / 80%)

        // search
        //----------------------------------------
        anime({
            targets: '.search-section',
            opacity: isReaload?[1]:[0, 1],
            duration: 1000,
            easing: 'easeInOutQuad',
        });

        // console.log(this.theme);
        await this.isBackgroundImageLoaded(this.theme.background);

        // page background
        //----------------------------------------
        this.backgroundArea.css('background-image', `url(${this.theme.background})`);
        !isReaload && await this.delay(1000);
        anime({
            targets: '.bg-section',
            opacity: [0, 1],
            scale: isReaload?[1, 1.1, 1] : [1.1, 1],
            easing: 'easeInOutQuad',
            duration: 2000,
        });


        
        if(this.theme.quote){
            //quote box bg
            //----------------------------------------
            if(this.quoteAreaBoxBgStatus){
                await this.delay(1000);
                anime({
                    targets: '.quote-section',
                    background: this.quoteAreaBoxBgColor,
                    duration: 500,
                    easing: 'easeInOutQuad',
                });
            }
            // quote area
            //----------------------------------------
            this.quoteArea.css('color', this.quoteTextColor);
            this.quoteArea.css('font-family', this.theme.quoteFont);
            this.quoteArea.css('font-size', this.theme.quoteFontSize);
            // this.quoteArea.text(this.theme.quote.split(';')[0]);
            this.quoteArea.text("\"" + this.theme.quote.split(';')[0]+"\"");
            anime({
                targets: '.quote',
                opacity: [0, 1],
                duration: 1000,
                easing: 'easeInOutQuad',
            });

            // author area
            //----------------------------------------
            await this.delay(500);
            this.authorArea.css('color', this.authorTextColor);
            this.authorArea.css('font-family', this.theme.authorFont);
            this.authorArea.css('font-size', this.theme.authorFontSize);
            // this.authorArea.text(this.theme.quote.split(';')[1]);
            if(this.theme.quote.split(';')[1])
                this.authorArea.text("- " + this.theme.quote.split(';')[1]+" -").slideDown('fast');
            anime({
                targets: '.author',
                opacity: [0, 1],
                duration: 1000,
                easing: 'easeInOutQuad',
            });

        }
    }

    
    async getTheme(){
        const randomNumber = Math.floor(Math.random() * this.quoteFontSizes.length);
        return {
            // quote: this.quoteList[Math.floor(Math.random() * this.quoteList.length)],
            // background: this.backgrounds[Math.floor(Math.random() * this.backgrounds.length)],
            quote: await fileMenagement.getRandomQuote(),
            background: await fileMenagement.getRandomBackground(),

            quoteFont: `quoteFont-${Math.floor(Math.random() * this.quoteFontList.length)}`,
            authorFont: `authorFont-${Math.floor(Math.random() * this.authorFontList.length)}`,
            
            quoteFontSize: this.quoteFontSizes[randomNumber],
            authorFontSize: this.authorFontSizes[randomNumber],
        };
    }

    addFonts(){
        this.quoteFontList.forEach((font, index) => {
            const fontFace = new FontFace(`quoteFont-${index}`, `url(${font})`);
            fontFace.load().then(loadedFace => {
                document.fonts.add(loadedFace);
            });
        });

        this.authorFontList.forEach((font, index) => {
            const fontFace = new FontFace(`authorFont-${index}`, `url(${font})`);
            fontFace.load().then(loadedFace => {
                document.fonts.add(loadedFace);
            });
        });
    }

    // alert when background image is loaded properly
    isBackgroundImageLoaded(imageUrl){
        return new Promise((resolve, reject) => {
            const img = new Image();
            img.src = imageUrl;
            img.onload = () => {
                resolve(true);  
            };
        });
    }

    // async loadFils(){
    //     // get backgrounds and quotes
    //     const versionMenagement = new VersionMenagement();
    //     const getQuoteFromCromeStorage = await versionMenagement.getQuoteFromCromeStorage();
    //     if(getQuoteFromCromeStorage){
    //         this.quoteList = getQuoteFromCromeStorage;
    //     }else{
    //         await versionMenagement.updateFileVersion();
    //         this.quoteList = await versionMenagement.getQuoteFromCromeStorage();
    //     }
        
    //     const getBackgroundsFromCromeStorage = await versionMenagement.getBackgroundsFromCromeStorage();
    //     if(getBackgroundsFromCromeStorage){
    //         this.backgrounds = getBackgroundsFromCromeStorage;
    //     }else{
    //         await versionMenagement.updateFileVersion();
    //         this.backgrounds = await versionMenagement.getBackgroundsFromCromeStorage();
    //     }
    // }

    
    async setConfigaration(){
        const configaration = await getConfigFile();
        this.quoteAreaBoxBgStatus = configaration.quoteAreaBoxBgStatus;
        this.quoteAreaBoxBgColor = configaration.quoteAreaBoxBgColor;
        this.quoteTextColor = configaration.quoteTextColor;
        this.authorTextColor = configaration.authorTextColor;
        this.quoteFontList = configaration.quoteFontList;
        this.authorFontList = configaration.authorFontList;
        this.quoteFontSizes = configaration.quoteFontSizes;
        this.authorFontSizes = configaration.authorFontSizes;
        this.addFonts();
    }

    async addFooterLink(){
        const configaration = await getConfigFile();
        if(configaration.shading){
            $('body').css('text-shadow', '0 0 10px rgb(0 0 0 / 80%)')
        }
        $('.menu .about').attr('href', configaration.footerLinks.about);
        $('.menu .privacy').attr('href', configaration.footerLinks.privacy);
        $('.menu .terms').attr('href', configaration.footerLinks.terms);
        $('.menu .uninstall').attr('href', configaration.footerLinks.uninstall);
        $('.menu .contact').attr('href', configaration.footerLinks.contact);
    }

    addEvent(){
        const self = this;
        $(document).on('click', 'span.realoadBtn', () => {
            self.changeBgAndQuate();
        });
    }

    async changeBgAndQuate(){
        this.theme = await this.getTheme();
        this.setTheme(true);
    }
   
    async init(){
        await this.addFooterLink();
        await this.setConfigaration();
        // await this.loadFils(); 
        this.theme = await this.getTheme();
        this.setTheme();
        this.addEvent();
    }

}