//my tasks class
// ----------------------------------------------
import $ from 'jquery';
export class Tasks {
    constructor() {
        this.tasks = {
            'Urgent': [],
            'Work': [],
            'Home': []
        };
        this.category = 'Urgent';
        this.isOpen = false;
        this.tasksListArea = $('.taskList');
        this.addTaskForm = $('.newTaskForm');
        this.addTaskInput = $('.newTaskInput');
        this.categoryListArea = $('.categoryListArea');
        this.cencelBtn = $('a.cencelBtn');
    }

    // save tasks to chrome storage
    saveTasks() {
        chrome.storage.sync.set(
            { 
                'tasks': this.tasks, 
                'category': this.category,
            }, 
            () => {
                console.log('Tasks saved');
            }
        );
    }

    // get tasks from chrome storage
    getTasksFromStorage() {
        return new Promise((resolve) => {
            chrome.storage.sync.get(['tasks', 'category', 'isOpen'], (result) => {
                if(result.tasks) {
                    this.tasks = result.tasks;
                }
                if(result.category) {
                    this.category = result.category;
                }
                if(result.isOpen == true || result.isOpen == false) {
                    this.isOpen = result.isOpen;
                }
                resolve();
            });
        });
    }

    addTask(category, task) {
        this.tasks[category].push(task);
        this.renderTasks();
        this.renderCategories();
        this.saveTasks();
    }   

    removeTask(category, timestemp) {  
        this.tasks[category] = this.tasks[category].filter(task => task.timestemp !== timestemp);
        this.renderTasks();
        this.renderCategories();
        this.saveTasks();
    }

    editTask({category, timestemp, newTask, isComplete}={}) {
        this.tasks[category] = this.tasks[category].map(task => {
            if(task.timestemp === timestemp) {
                task.task = newTask? newTask : task.task;
                task.isComplete = isComplete;
            }
            return task;
        });
        this.renderTasks();
        this.saveTasks();
    }

    getTasks() {    
        return this.tasks[this.category];
    }   

    changeCategory(category) {
        this.category = category;
        this.renderTasks();
        this.renderCategories();
        $('.current_category').text(this.category);
        this.saveTasks();
    }

    addCategory(category) {
        this.tasks[category] = [];
        this.changeCategory(category);
        this.saveTasks();
    }

    // render categories
    renderCategories() {
        let categories = Object.keys(this.tasks);
        const k = categories.map(category => `
            <li category="${category}" class="item">
                <div>
                    <span>${category}</span>
                    <span class="counter">${this.getCategoryLength(category)}</span>
                </div>
                <a href="#" class="categoryDeleteBtn">
                    <img src="images/icons8-delete-25.png">
                </a>
            </li>`);
        
        k.push(`
            <li>
                <input type="text" class="categoryInput" placeholder="+ Add New">
                <a href="#" class="categoryAddBtn" style="display: none;"><img src="images/icons8-tick-24.png" alt=""></a>
            </li>
        `);
        this.categoryListArea.html(k.join(''));
    }

    // delete category
    deleteCategory(category) {
        this.tasks = Object.keys(this.tasks).reduce((acc, key) => {
            if(key !== category) {
                acc[key] = this.tasks[key];
            }
            return acc;
        }, {});
        console.log(this.tasks);
        this.changeCategory('Urgent');
        this.renderCategories();
        this.renderTasks();
        this.saveTasks();
    }

    // get category length
    getCategoryLength(category) {
        return this.tasks[category].length;
    }

    // render tasks
    renderTasks() {
        const tasks = this.getTasks();
        if(tasks.length){
            var ui = tasks.map((task, index)=>{
                return `
                    <li class="taskItem" timestemp="${task.timestemp}">
                        <div class="left">
                            <input type="checkbox" class="taskCheckbox" ${task.isComplete && 'checked'}>
                            <div class="taskItem__text ${task.isComplete && 'completeTask'}">${task.task}</div>
                            <input type="text" class="editInput" value="${task.task}">
                        </div>
                        <div class="right">
                            <img src="images/icons8-dots-67.png">
                            <ul class="itemAction">
                                <li class="editBut">edit</li>
                                <li class="delete">delete</li>
                            </ul>
                        </div>
                    </li>
                `;
            }).join('');
            this.tasksListArea.html(ui);
            $('.newTaskInput').show().focus();
        }else{
            var ui = `
            <div class="emptyBox">
                <p>Add a todo to get started</p>
                <button>New Todo</button>
            </div>
            `;
            this.tasksListArea.html(ui);
            $('.newTaskInput').hide();
        }
    }

    // add task
    addTaskEvent() {
        const self = this;

        this.addTaskForm.on('submit', (e) => {
            e.preventDefault();
            const task = self.addTaskInput.val();
            if(task == '') return;
            self.addTask(self.category, {isComplete: false, task: task, timestemp: new Date().getTime().toString()});
            self.addTaskInput.val('').focus();
            $('.newTaskAddBtn').hide();
        });
        $(document).on('click', '.newTaskAddBtn', function(e) {
            e.preventDefault();
            const task = self.addTaskInput.val();
            if(task == '') return;
            self.addTask(self.category, {isComplete: false, task: task, timestemp: new Date().getTime().toString()});
            self.addTaskInput.val('').focus();
            $('.newTaskAddBtn').hide();
        });
        $(document).on('keydown', '.newTaskInput', function(e) {
            const value = $(this).val();
            if(value != '') {
                $('.newTaskAddBtn').show();
            }else{
                $('.newTaskAddBtn').hide();
            }
        });

        // if click on the delete
        $(document).on('click', '.taskItem .itemAction li.delete', function() {
            const parent = $(this).parent().parent().parent();
            const timestemp = parent.attr('timestemp');
            self.removeTask(self.category, timestemp);
        });

        // if click on the checkbox
        $(document).on('click', '.taskItem .taskCheckbox', function() {
            const parent = $(this).parent().parent();
            const timestemp = parent.attr('timestemp');
            const isComplete = $(this).is(':checked');
            self.editTask({category: self.category, timestemp, isComplete});
            parent.toggleClass('active');
        });

        // if click on the edit
        $(document).on('click', '.taskItem .itemAction li.editBut', function() {
            const parent = $(this).parent().parent().parent();
            parent.toggleClass('active');
            parent.find('.taskItem__text').hide();
            parent.find('.editInput').show();
            // focus on the input on the last char
            const input = parent.find('.editInput');
            const val = input.val();
            input.focus().val('').val(val);
        });

        // if click enter on the edit input
        $(document).on('keypress', '.taskItem .editInput', function(e) {
            if(e.which == 13) {
                const parent = $(this).parent().parent();
                const timestemp = parent.attr('timestemp');
                const newTask = $(this).val();
                self.editTask({category: self.category, timestemp, newTask});
                parent.find('.taskItem__text').show();
                parent.find('.editInput').hide();   
            }
        });

        //onc click on the right
        $(document).on('click', '.taskItem .right', function(event) {
            // target not .itemAction li.editBut
            if(!$(event.target).is('.itemAction li.editBut')){
                $('.taskItem').removeClass('active');
                $(this).parent().addClass('active');
            }
        });

        // if click on the body
        $(document).on('click', function(e) {
            if (!$(e.target).closest('.taskItem .right').length) {
                $('.taskItem').removeClass('active');
                $('.taskItem .taskItem__text').show();
                $('.taskItem .editInput').hide();
            }
            if(!$(e.target).closest('.addCategory').length && !$(e.target).is('.categoryInput')) {
                $('.tasksListArea .headSection').removeClass('active');
            }
        });

        // if click on the category
        $(document).on('click', '.addCategory', function(event) {
            $('.tasksListArea .headSection').toggleClass('active');  
        });

        // if click on the category item
        $(document).on('click', '.categoryListArea .item', function() {
            const category = $(this).attr('category');
            self.changeCategory(category);
            $('.tasksListArea .headSection').removeClass('active');
        });

        // if click on the category delete
        $(document).on('click', '.categoryListArea .categoryDeleteBtn', function() {
            const category = $(this).parent().attr('category');
            self.deleteCategory(category);
        });

        //current category
        $('.current_category').text(this.category);

        // if click on the add new category
        $(document).on('click', '.categoryInput', function() {
            //change placeholder
            $(this).attr('placeholder', 'Type new category...');
        });

        // if click on the add new category categoryInput
        $(document).on('keypress', '.categoryListArea .categoryInput', function(e) {
            if(e.which == 13) {
                const category = $(this).val();
                if(category == '') return;
                self.addCategory(category);
                $(this).val('');
                $('.tasksListArea .headSection').removeClass('active');
                $('.categoryAddBtn').hide();
            }
        });
        $(document).on('click', '.categoryAddBtn', function(e) {
            e.preventDefault();
            const category = $('.categoryInput').val();
            if(category == '') return;
            self.addCategory(category);
            $(this).val('');
            $('.tasksListArea .headSection').removeClass('active');
            $('.categoryAddBtn').hide();
        });
        $(document).on('keydown', '.categoryInput', function(e) {
            const value = $(this).val();
            if(value != '') {
                $('.categoryAddBtn').show();
            }else{
                $('.categoryAddBtn').hide();
            }
        });

        // when click on empty box button
        $(document).on('click', '.emptyBox button', function() {
            $(this).hide();
            $('.newTaskInput').show().focus();
            $('.newTaskAddBtn').show();
        });


        // toggle task box
        $(document).on('click', '.mytasksBtn', function() {
            $('.tasksListArea').toggle();
            chrome.storage.sync.set({'isOpen': !self.isOpen }, function() {
                self.isOpen = !self.isOpen;
            });
        })

        // when click on the close button
        this.cencelBtn.on('click', function() {
            $('.tasksListArea').hide();
            $('.emptyBox button').show();
            chrome.storage.sync.set({'isOpen': !self.isOpen }, function() {
                self.isOpen = !self.isOpen;
            });
        })

    }

    checkPopupStats() {
        if(this.isOpen) {
            $('.tasksListArea').show();
        }else{
            $('.tasksListArea').hide();
        }
    }

    async init() {
        await this.getTasksFromStorage();
        this.renderTasks();
        this.addTaskEvent();
        this.renderCategories();
        this.checkPopupStats();
    }

}