import $ from 'jquery';

export class SearchSuggestions{
    constructor(){
        this.searchInput = $('#myInput');
        this.suggestionsAra = $('.suggestion-area');
        this.searchHistory = [];
    }

    getSearchHistory(text) {
        return new Promise((resolve)=>{
            chrome.history.search({text: text, maxResults: 10}, function (data) {
                var history = data.map((value, index)=>{
                    return {
                        title: value.title,
                        url: value.url
                    }
                }); 
                resolve(history);
            });
        });
    }

    getFaviconUrl(url) {
        return `chrome-extension://${chrome.runtime.id}/_favicon/?pageUrl=${encodeURIComponent(url)}&size=32`;
    }
    
    getTopSites() {
        return new Promise(function (resolve, reject) {
            chrome.topSites.get(function (data) { 
                var topSites = data.map((value, index)=>{
                    return {
                        title: value.title,
                        url: value.url
                    }
                }); 
                resolve(topSites);
            });
        });
    }

    showSuggestions(results){
        const self = this;
        const ui = results.map((value, index)=>{
            return `<a href="${value.url}" target="_blank">
                <div class="left"><img src="${self.getFaviconUrl(value.url)}" alt=""></div>
                <div class="right">
                    <div>${self.shortString(value.title, 30)}</div> &nbsp;-&nbsp;
                    <div class="link">${self.shortString(value.url, 15)}</div>
                </div>
            </a>`;
        });
        this.suggestionsAra.html(ui.join(''));
    }

    shortString(str, length){
        if (str.length > length) {
            return str.substring(0, length) + '...';
        }
        return str;
    }
    
    addEvents(){
        const self = this;
        $(document).on('click', (e)=>{
            if (!$(e.target).closest('.search-section').length) {
                this.suggestionsAra.html('');
                this.suggestionsAra.hide();
            }
        });
        this.searchInput.on('keyup', async (e)=>{
            let val = e.target.value;
            if (val.length == 0) {
                this.suggestionsAra.hide();
                return false;
            }
            const results = [] // await this.getSearchHistory(val);
            if(results.length){
                this.suggestionsAra.show();
                self.showSuggestions(results);
            }else{
                this.suggestionsAra.hide();
            }
        });

        // on mouse left click
        this.searchInput.on('click', async (e)=>{
            const results = []; //await this.getTopSites();
            if(results.length){
                self.suggestionsAra.show();
                self.showSuggestions(results);
            }else{
                self.suggestionsAra.hide();
            }
        });

    }

    init(){
        this.addEvents();
    }
}
