import { getConfigFile } from "./ablibary";

export class FileMenagement{
    constructor(){

    }
    
    // get background images from csv file using fetch api
    async getBackgroundLinkFile(){
        const configaration = await getConfigFile();
        const request = await fetch(configaration.backgroundsUrl);
        const data = await request.text();
        const rows = data.split('\n');
        const backgrounds = rows.map(row => row.split(',')[0]);
        // console.log(backgrounds);
        // remove all empty item
        const backgroundsFiltered = backgrounds.filter(item => item !== '');
        console.log(backgroundsFiltered);
        return backgroundsFiltered;
    }

    async getRandomBackground(){
        const bgLinks = await this.getBackgroundLinkFile();
        const randomBg = bgLinks[Math.floor(Math.random() * bgLinks.length)];
        // check if file have http
        if(randomBg.includes('http')){
            return randomBg;
        }else{
            const request = await fetch('./'+randomBg);
            const blob = await request.blob();
            const file = new File([blob], 'image.jpg');
            return URL.createObjectURL(file);
        }
    }

    async getRandomQuote(){
        const configaration = await getConfigFile();
        try {
            const request = await fetch(configaration.quotesUrl).catch(error => console.log(error));
            const data = await request.text();
            const quotes = data.split('\n');
            const quotesFiltered = quotes.filter(item => item !== '');
            return quotesFiltered[Math.floor(Math.random() * quotesFiltered.length)];
        } catch (error) {
            return null;
        }
    }
}