'use strict';

import { doSearch, showDate, showTime } from './ablibary';
import './newTab.css';
import $ from 'jquery';
import { Tasks } from './myTask';
import { Themes } from './themes';
import { SearchSuggestions } from './suggestion';
import { weather } from './weather';
import { StickyNote } from './stickyNote';
import { AppLinks } from './lisnks/app_links';
import { MostVisited } from './lisnks/mostVisited';
import { RecentlyClosed } from './lisnks/recentlyClosed';
import { SideBarLink } from './lisnks/sideBarLink';

(async () => {

    
    // uninstall extension
    // -------------------
    $('#uninstall').on('click',function () {
        chrome.management.uninstallSelf({
            showConfirmDialog: true
        });
    });

    // app links
    // -------------------
    const appLinks = new AppLinks();
    appLinks.init();

    // Most Visited
    // -------------------
    const mostVisited = new MostVisited();
    mostVisited.init();

    // Recently Closed
    // -------------------
    const recentlyClosed = new RecentlyClosed();
    recentlyClosed.init();

    // Recently Closed
    // -------------------
    const sideBarLink = new SideBarLink();
    sideBarLink.init();

    // StickyNote
    // -------------------
    const stickyNote = new StickyNote();
    stickyNote.init();

    /*SearchSuggestions*/
    const searchSuggestions = new SearchSuggestions();
    searchSuggestions.init();
    

    //onclick search button
    document.querySelector('.searchIcon').addEventListener('click',function () {
        doSearch();
    });
    $('.search-section form').on('submit', function(e) {
        e.preventDefault();
        doSearch();
    })

    // show current date on this formate Saturday 08 October
    // ----------------------------------------------------
    document.querySelector('.date').innerHTML = showDate();

    // show current time on this formate 10:10
    // ----------------------------------------------------
    document.querySelector('.time').innerHTML = showTime();
    setInterval(function() {
        document.querySelector('.time').innerHTML = showTime();
    }, 1000);
    
    // show weather()
    // ----------------------------------------------------
    // weather();
    new weather('.weatherArea');

    // show tasks
    // ----------------------------------------------------
    const app = new Tasks();
    app.init();

    // setup themes
    // ----------------------------------------------------
    const theme = new Themes();
    theme.init();
    

    
    


 


})();
