
import moment from 'moment';
export async function doSearch() {
    var searchTerm = document.getElementById('myInput').value
    if (searchTerm == '') {
        return false;
    }
    const configaration = await getConfigFile();
    // var engineUrl = 'https://www.google.com/search?q=';
    //'https://search.my-daily-inspiration.com/search.php?q=';
    // https://www.bing.com/search?q=<keyword>
    // remove <keyword> from the url and add it to the end of the url
    var engineUrl = configaration.searchURL
    engineUrl = engineUrl.replace('<keyword>', searchTerm);
    // engineUrl += searchTerm;
    window.location.href = engineUrl;
    return false;
}

export function showDate() {
    let date = new Date();
    let days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
    let day = days[date.getDay()];
    let month = date.toLocaleString('default', { month: 'long' });
    let dayNumber = date.getDate();
    let year = date.getFullYear();
    let fullDate = day + ' ' + dayNumber + ' ' + month //+ ' ' + year;
    return fullDate;
}

export function showTime() {
    // return time on this formate 9:04
    // let date = new Date();
    // let hours = date.getHours();
    // let minutes = date.getMinutes();
    // let seconds = date.getSeconds();
    // let time = hours + ':' + minutes //+ ':' + seconds;
    return moment().format("HH:mm");;
}

