import $ from 'jquery';
import "./mostVisited.css"
export class MostVisited {
    constructor() {
        this._mostVisited = [];
    }

    ui(topSitesList){
        const self = this;
        const list = topSitesList.map((value, index)=>{
            return `
                <li class="mostVisited__item">
                    <img src="${self.getFaviconUrl(value.url)}" />
                    <a href="${value.url}" target="__blank">${value.title}</a>
                </li>
            `;
        }).join('');
        $('.mostVisited__area').append(list);
    }

    getTopSites() {
        return new Promise(function (resolve, reject) {
            chrome.topSites.get(function (data) { 
                var topSites = data.map((value, index)=>{
                    return {
                        title: value.title,
                        url: value.url
                    }
                }); 
                resolve(topSites);
            });
        });
    }


    // chrome.permissions.request
    getPermissions() {
        return new Promise(function (resolve, reject) {
            chrome.permissions.request({
                permissions: ['topSites', 'favicon']
            }, function (granted) {
                if (granted) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            });
        });
    }

    getFaviconUrl(url) {
        return `chrome-extension://${chrome.runtime.id}/_favicon/?pageUrl=${encodeURIComponent(url)}&size=16`;
    }

    addEvents(){
        const self = this;
        $(document).on('click', '.mostVisited_item', async (e)=>{
            $('.mostVisited__area').toggle();
            $('.mostVisited__area').html('');
            const sessionsPermissions = await self.getPermissions();
            if (sessionsPermissions) {
                const topSitesList = await self.getTopSites();
                self.ui(topSitesList);
            } else {
                console.log('sessionsPermissions not granted');
            }
        });

        // if click outside of the element, hide it
        $(document).on('click', function (e) {
            if (!$(e.target).closest('.mostVisited_item').length) {
                $('.mostVisited__area').hide();
            }
        });
    }
           

    async init(){
        this.addEvents();
    }
}